<template>
  <SubModal v-model:isVisible="modalVisible">
    <div class="modal-close" @click="closeCreate"><img alt="" src="../assets/icons/cancel.png"></div>
    <div class="modal__content-title"> :</div>
    <ClientsObjectList
        :objects="objectList.values"
        :title="objectList.title"
        @selected="objectList.selected = $event"/>
    <DatabaseObject v-for="(object, index) in databaseObjects"
                    :key="index"
                    :objects="object.values"
                    :title="object.title"
                    @update:selected="updateSelected(object, $event)"
                    @update:objects="object.values = $event"/>
  </SubModal>
</template>

<script>
import DatabaseObject from './DatabaseCreate/DatabaseObject.vue';
import DatabaseObjectApi from '../api/DatabaseObjectApi';
import UploadApi from "@/api/UploadApi";
import ClientsObjectList from "@/Blocks/ClientsObjectList.vue";
import ClientsApi from "@/api/ClientsApi";
import updateObject from "@/Blocks/UpdateObject.vue";

export default {
  name: 'affairs-create',
  props: {
    show: Boolean,
  },
  components: {ClientsObjectList, DatabaseObject},
  emits: ['update:isVisible'],
  data() {
    return {
      modalVisible: false,
      objectList: {},
      databaseObjects: {}
    }
  },
  mounted() {
    this.modalVisible = this.show;
    this.constructorModal();
  },
  watch: {
    show(newVal) {
      this.modalVisible = newVal;
    },
    modalVisible(newVal) {
      this.$emit('update:show', newVal);
    },
    // async allSelectedFilled(newVal) {
    //   console.log(newVal)
    //   if (newVal.isValid) {
    //     if (newVal.data.trademark.name.split('.').length > 1) {
    //       newVal.data.trademark = (await UploadApi.upload(newVal.data.trademark)).data.fileName;
    //     }
    //     let client = {};
    //     if (this.objectList.selected) {
    //       client = (await ClientsApi.create(this.objectList.selected.name)).data.id;
    //     }
    //     const object = await DatabaseObjectApi.create({...newVal.data, client});
    //     if (object.data.id !== undefined) {
    //       this.$emit('update:objectSelect', object.data);
    //     }
    //   }
    // },
  },
  computed: {
    updateObject() {
      return updateObject
    },
    allSelectedFilled() {
      const data = {};
      let allFilled = true;

      for (const key in this.databaseObjects) {
        const object = this.databaseObjects[key];

        if (object.selected === '' || object.selected === undefined) {
          allFilled = false;
          break;
        } else if (object.selected.name.split('.').length > 1) {
          data[key] = object.selected;
        } else {
          data[key] = object.selected.id ? object.selected.id : object.selected.name;
          data[key] = data[key].split('.').length > 1 ? object.selected : data[key];
        }
      }

      return {
        selected: allFilled,
        data: allFilled ? data : {},
      };
    }
  },
  methods: {
    updateSelected(object, event) {
      object.selected = event
      this.updateAllSelectedFilled();
    },
    async updateAllSelectedFilled() {
      let allSelected = this.allSelectedFilled;
      if (allSelected.selected) {
        if (allSelected.data.trademark.name.split('.').length > 1) {
          allSelected.data.trademark = (await UploadApi.upload(allSelected.data.trademark)).data.fileName;
        }
        if (this.objectList.selected) {
          allSelected.data.client = (await ClientsApi.create({name: this.objectList.selected.name})).data.id;
        }
        const object = await DatabaseObjectApi.create(allSelected.data);
        if (object.data.id !== undefined) {
          this.$emit('update:objectSelect', object.data);
        }
      }
    },
    async constructorModal() {
      this.objectList = {
        title: '',
        values: [
          {name: '  ', action: 'insert'},
          {name: ' ', action: 'objectList'}
        ],
        selected: {}
      }
      this.databaseObjects = {
        name: {
          title: ' ',
          values: [
            {action: 'text'},
          ],
          selected: ''
        },
        trademark: {
          title: ' ',
          values: [
            {name: '', action: 'file', options: false},
            {name: '', action: 'text', options: false}
          ],
          selected: ''
        },
        territory: {
          title: '',
          values: [
            {name: ' (KZ)'},
          ],
          selected: ''
        },
        nonProtectedElements: {
          title: ' ',
          values: [
            {action: 'text'},
          ],
          selected: ''
        },
        numberOrder: {
          title: ' ',
          values: [
            {action: 'text'},
          ],
          selected: ''
        },
        dateOrder: {
          title: ' ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        numberRegistration: {
          title: ' ',
          values: [
            {action: 'text'},
          ],
          selected: ''
        },
        dateRegistration: {
          title: ' ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        datePublication: {
          title: ' ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        priorityNumber: {
          title: ' ',
          values: [
            {action: 'text'},
          ],
          selected: ''
        },
        priorityDate1: {
          title: ' ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        priorityDate2: {
          title: ' ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        validityStart: {
          title: '  ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        validityEnd: {
          title: '  ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        extensionStart: {
          title: '  ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        extensionEnd: {
          title: '  ',
          values: [
            {action: 'date', options: false},
          ],
          selected: ''
        },
        niceClasses: {
          title: ' ',
          values: [
            {action: 'text'},
          ],
          selected: ''
        },
      }
    }
  },
  closeCreate() {
    this.modalVisible = false;
  },
}
</script>   